body {
    background-color: #FFFCF2;
}

body, ul, li, p {
    margin: 0px;
    padding: 0px;
    list-style: none;
    font-size: 1.2rem;
    font-family: Arial, Helvetica, sans-serif;
}

h1, h2, h3, h4 {
    color: #EB5E28;
    font-weight: bold;
}

textarea {
    font-family: monospace, monospace;
    font-size: 20px;
    width: 100%;
    height: 80vh;
    resize: none;
    background-color: #403D39;
    color: #FFFCF2;
}

a {
    text-decoration: none;
    color: #EB5E28;
}

.header {
    background-color: #252422;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.menu {
    display: flex;
}

.menu li {
    margin-left: 20px;
}

.menu li a {
    display: block;
    padding: 10px;
}

.content {
    display: grid;
    grid-template-columns: 150px 1fr 150px 1fr;
    margin: 0 auto;
}

.content div {
    padding: 15px;
}

.request-options {
    text-align: center;
}

.button-start {
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-start button {
    cursor: pointer;
    font-size: 20px;
    background-color: #EB5E28;
    padding: 20px;
    border: none;
    border-radius: 10px;
}

.loader-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    border: 16px solid #403D39; /* Light grey */
    border-top: 16px solid #EB5E28; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    align-items: center;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.error-panel p {
    margin: 10px;
}

.error-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.error {
    font-family: monospace, monospace;
    font-weight: bold;
    background-color: #EB5E28;
    color: #252422;
    border-radius: 10px;
}

.message {
    display: flex;
    justify-content: center;
    align-items: center;
}

.message p {
    padding: 10px;
    background-color: #EB5E28;
    color: #252422;
    font-weight: bold;
    border-radius: 10px;
}